export const getCricketFixture = (page) => {
  return async (dispatch) => {
    // dispatch(getAllFixtureInit())
    await http.get(`/cricket/fixture/list?page=${page}`).then((response) => {     
      dispatch({
        type: 'CRICKET_FIXTURE',
        data: response.data.data
      })
    }).catch(() => {      
      dispatch(getAllCricketcontestFail())
    })
  }
}

