export const getCricketFixtureAdd = (add) => {
  return (dispatch) => {
    http
    .post('/cricket/fixture/add', add)
    .then((response) => {
      notifySuccessadd(response.data.message)
      dispatch({
        type: 'CRICKETFIXTURE_ADD',
        data: response.data
      })    
    })
    .catch((err) => {
     notifyError(err.response.data.message)
    })
  }
}

export const deletefixturecricket = (id) => {
  return (dispatch) => {
     return http.delete(`cricket/fixture/delete/${id}`).then((response) => {
      notifySuccessadd("successfully deleted")
       dispatch({ type: 'DELETE_CRICKET_FIXTURE', data: response.data.data })
       dispatch(getCricketFixture())
     })
     .catch(() => {
      notifyError('error')
     })
   }
 }

