export const fixturetoggle = (id, status) => {
  return async (dispatch) => {
    // dispatch(getAllFixtureInit())
    await http.post(`/cricket/fixture/toggle_active/${id}`).then(() => {
    notifySuccessadd('Successfully Updated')    
    dispatch({
        type: 'FIXTURE_TOGGLE_STATUS',
        data: status
      })           
    }).catch((e) => {
      notifyError(e.message ? e.message : 'error')
    })
  }
}

