  export const getcricketfixturedownload = (series_id = '', fixture_id = '', search_type = '', search_status = '', from = '', to = '') => {
    // const today = new Date()
    // const date = `${today.getFullYear()}_${(today.getMonth() + 1)}_${today.getDate()}`
    return async (dispatch) => {
      dispatch(getdownload())
      await http
        .get(from && to ? `/cricket/fixture/list?series_id=${series_id}&fixture_id=${fixture_id}&type=${search_type}&fixture_status=${search_status}&from=${from}&to=${to}&download=true` : '/cricket/reports/contestReport?download=true', { responseType: 'blob' })
        .then((response) => {
          notifySuccessadd('succesfully downloaded')
          const url = window.URL.createObjectURL(new Blob([response.data]))
          const link = document.createElement('a')
          link.href = url
          link.setAttribute('download', `cricketFixture.csv` /*`contest.csv${date}`*/) 
          document.body.appendChild(link)
          link.click()
          dispatch({
            type:'SET_DOWNLOAD_OFF'
          })
        })
        .catch(() => {
          dispatch({
            type:'SET_DOWNLOAD_OFF'
          })
          notifyError("Download Failed")
        })
    }
  }
  export const getfootballfixturedownload = (series_id = '', fixture_id = '', fixture_status = '', from = '', to = '') => {
    return async (dispatch) => {
      dispatch(getdownload())
      await http
        .get(from && to ? `/football/fixture/list?series_id=${series_id}&fixture_id=${fixture_id}&fixture_status=${fixture_status}&from=${from}&to=${to}&download=true` : '/cricket/reports/contestReport?download=true', { responseType: 'blob' })
        .then((response) => {
          notifySuccessadd('succesfully downloaded')
          const url = window.URL.createObjectURL(new Blob([response.data]))
          const link = document.createElement('a')
          link.href = url
          link.setAttribute('download', `footbalFixture.csv` /*`contest.csv${date}`*/) 
          document.body.appendChild(link)
          link.click()
          dispatch({
            type:'SET_DOWNLOAD_OFF'
          })
        })
        .catch(() => {
          dispatch({
            type:'SET_DOWNLOAD_OFF'
          })
          notifyError("Download Failed")
        })
    }
  }

