// ** React Imports
import { Fragment, useState, useEffect, memo } from 'react'
import UiLoader from '@components/ui-loader'
// ** Table Columns
// import AppCollapse from '@components/app-collapse'
// ** Store & Actions
import { fixturecricketshow  } from '../../store/actions'
import { useSelector, useDispatch } from 'react-redux'
import {useHistory, useParams} from 'react-router-dom'
// import withReactContent from 'sweetalert2-react-content'
// import Swal from 'sweetalert2' 
// ** Styles
import '@styles/react/libs/react-select/_react-select.scss'
import '@styles/react/libs/tables/react-dataTable-component.scss'

// ** Third Party Components
import ReactPaginate from 'react-paginate'
import { ChevronDown, Filter } from 'react-feather'
import DataTable from 'react-data-table-component'
import { Card, Input, Label, Row, Col } from 'reactstrap'
const DataTableTransaction = () => {
  // const MySwal = withReactContent(Swal)
  // ** Store Vars
const history = useHistory()
  const dispatch = useDispatch()
  const params = useParams()
  const store = useSelector(state => state.fix)
  
  console.log(store.cricket_fixture)
  // ** States

  const [currentPage, setCurrentPage] = useState(1)
  const [rowsPerPage, setRowsPerPage] = useState(10)
  const [searchValue, setSearchValue] = useState('')
  const [filteredData, setFilteredData] = useState([])
  
  useEffect(() => {                
    dispatch(fixturecricketshow(params.id, currentPage))
  }, [currentPage])
  
  const columns = [
    {
      name: '#',
      minWidth: '10px',
      selector: '#',
      sortable: true,
      cell: (row, pos) => (`#${store.cricket_fixture_page.from + pos}`)
    }, 
    {
      name: 'Captions',
      minWidth: '20px',
      selector: 'caption',
      sortable: true,
      cell: (row) => row.caption
    },
    {
      name: 'Contest name',
      minWidth: '20px',
      selector: 'contest_name',
      sortable: true,
      cell: (row) => row.contest_name
    },
    {
      name: 'Entry fee',
      minWidth: '20px',
      selector: 'entry_fee',
      sortable: true,
      cell: (row) => row.entry_fee
    },
    {
      name: 'Contest tye',
      minWidth: '20px',
      selector: 'contest_type',
      sortable: true,
      cell: (row) => row.contest_type
    },
    {
      name: 'Prize type',
      minWidth: '20px',
      selector: 'prize_types',
      sortable: true,
      cell: (row) => row.prize_types
    }
  ]
  // ** Function to handle filter
  const handleFilter = e => {
    const value = e.target.value
    let updatedData = []
    setSearchValue(value)
    if (value.length) {
      updatedData = store.cricket_fixture.filter(item => {        
        console.log(item)
       const startsWith =
          item.type?.toLowerCase().startsWith(value.toLowerCase()) ||
           item.position?.toString().toLowerCase().startsWith(value.toLowerCase()) 
          // item.debit_credit?.toLowerCase().startsWith(value.toLowerCase()) ||
          // date?.toString().toLowerCase().startsWith(value.toLowerCase()) ||
          // item.transaction_type?.toLowerCase().startsWith(value.toLowerCase()) ||
          // item.txn_id.toLowerCase()?.startsWith(value.toLowerCase())

        const includes =
          item.type?.toLowerCase().includes(value.toLowerCase()) ||
           item.position?.toString().toLowerCase().includes(value.toLowerCase()) 
          // item.debit_credit?.toLowerCase().includes(value.toLowerCase()) ||
          // date?.toString().toLowerCase().includes(value.toLowerCase()) ||
          // item.transaction_type?.toLowerCase().includes(value.toLowerCase()) ||
          // item.txn_id?.toLowerCase().includes(value.toLowerCase())

        if (startsWith) {
          return startsWith
        } else if (!startsWith && includes) {
          return includes
        } else return null
      })
      setFilteredData(updatedData)
    }
  }

  // ** Function to handle Pagination and get data

  const handlePagination = page => {
    setCurrentPage(page.selected + 1)
  }

  // ** Function to handle per page

  const handlePerPage = e => {
    setRowsPerPage(parseInt(e.target.value))
  }

  // ** Custom Pagination
  const CustomPagination = () => {

     const count = store.cricket_fixture_page.last_page
    return (
      <ReactPaginate
        previousLabel={''}
        nextLabel={''}
        breakLabel='...'
        pageCount={count}
        marginPagesDisplayed={2}
        pageRangeDisplayed={2}
        activeClassName='active'
        forcePage={currentPage !== 0 ? currentPage - 1 : 0}
        onPageChange={page => handlePagination(page)}
        pageClassName={'page-item'}
        nextLinkClassName={'page-link'}
        nextClassName={'page-item next'}
        previousClassName={'page-item prev'}
        previousLinkClassName={'page-link'}
        pageLinkClassName={'page-link'}
        breakClassName='page-item'
        breakLinkClassName='page-link'
        containerClassName={
          'pagination react-paginate separated-pagination pagination-sm justify-content-end pr-1 mt-1'
        }
      />
    )
  }
console.log(store.cricket_fixture)
  // ** Table data to render
  const dataToRender = () => {
    if (store.cricket_fixture?.length > 0) {
      return searchValue.length ? filteredData : store.cricket_fixture?.slice(0, rowsPerPage)
    } else {
      return []
    }
  }

  if (store.isloading) {   
    return (
      <div className="position-absolute d-flex justify-content-center align-items-center w-100 position-top-0 position-bottom-0 position-left-0 position-right-0 h-100">
        <UiLoader
          blocking
        />
      </div>
    )
  }
console.log(store)
  const data = [
    {
      title: <div><Filter size={18}/> Filter</div>,
      content: (
        <Row xl={12}>
          <Col
          xl="12"
          className="d-flex align-items-sm-center justify-content justify-content-start flex-lg-nowrap flex-wrap flex-sm-row flex-column pr-lg-1 p-0 mt-lg-0 mt-1"
        >
          <div className="d-flex align-items-center mb-sm-0 mb-1 mr-1 ml-1">
            <Label className="mb-0 mr-1" for="search-invoice">
              Search:
            </Label>
            <Input
              className='dataTable-filter'
              type='text'
              bsSize='sm'
              id='search-input'
              value={searchValue}
              onChange={handleFilter}
              placeholder='search...'
            />
          </div>
          
       </Col>
        </Row>
      )
    }
  ]
 console.log(data)
  return (
    <Fragment>
      {/* <Card>
      <AppCollapse data={data} type='border' accordion />
      </Card>      */}
      <Card>
       <Row className='mx-0 mt-1 mb-50'>
          <Col sm='6'>
            <div className='d-flex align-items-center'>
              <Label for='sort-select'>show</Label>
              <Input
                className='dataTable-select'
                type='select'
                id='sort-select'
                value={rowsPerPage}
                onChange={e => handlePerPage(e)}
              >
                <option value={10}>10</option>
                <option value={50}>50</option>
                <option value={100}>100</option>
              </Input>
              <Label for='sort-select'>entries</Label>
            </div>
          </Col>
        </Row>
        <DataTable
          noHeader
          pagination
          paginationServer      
          className='react-dataTable'
          columns={columns}
          sortIcon={<ChevronDown size={10} />}
          paginationComponent={CustomPagination}
          data={dataToRender()}
          onRowClicked={(data) => {
            history.push(`/cricketchallenge/${data.id}`)
          }}
        />
      </Card>
    </Fragment>
  )
}

export default memo(DataTableTransaction)
